body {
    background: #332741;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.renderCanvas {
    width: 880px;
    height: 100%;
    touch-action: none;
    float: left;
}

.AppContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    width: 1280px;
    height: 720px;
    background: #6b6b6b;
    box-shadow: 0px 0px 20px #000000;
}